@media print {
  /* your print styles */
  .raleigh-campaign.Campaign.CampaignType--floating div,
  .site-header .header-right-column,
  #page .site-main .post-nav-wrapper,
  #page .site-main .social-share-wrapper,
  aside.sidebar .location-callout-box-basic,
  aside.sidebar .featured-posts-block,
  aside.sidebar .categories-block,
  #page .site-main .call-to-action,
  footer.primary-footer {
    display: none;
  }
}

.in-page-nav-bar {
  position: relative;
  z-index: 10;
}

.in-page-nav-bar.sticky {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10000;
}

@media (min-width: 768px) {
  .in-page-nav-bar {
    background-color: #053b4c;
  }
}

.in-page-nav-bar ul {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  text-align: center;
}

.in-page-nav-bar .in-page-nav-toggle {
  position: relative;
  background-color: #084C61;
  padding: 8px 0;
  color: #FFF;
}

.in-page-nav-bar .in-page-nav-toggle > div:first-of-type {
  font-size: 16px;
}

.in-page-nav-bar .in-page-nav-toggle .active-page-link {
  font-size: 20px;
  line-height: 25px;
}

.in-page-nav-bar .in-page-nav-toggle i {
  position: absolute;
  right: 16px;
  bottom: 10px;
}

.in-page-nav-bar .in-page-links {
  width: 100%;
  /*display: block;*/
  list-style-type: none;
  padding: 0;
  background-color: #FFF;
  box-shadow: rgba(34, 34, 34, 0.1) 0px 1px 3px 2px;
}

@media (min-width: 768px) {
  .in-page-nav-bar .in-page-links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: unset;
    background-color: transparent;
  }
}

.in-page-nav-bar .in-page-links li {
  margin-bottom: 0;
}

@media (min-width: 576px) {
  .in-page-nav-bar .in-page-links li {
    padding: 7px 0;
  }
}

.in-page-nav-bar .in-page-links li .in-page-icon {
  padding-bottom: 0.25rem;
  display: none;
}

@media (min-width: 768px) {
  .in-page-nav-bar .in-page-links li .in-page-icon {
    display: block;
  }
}

@media (min-width: 768px) {
  .in-page-nav-bar .in-page-links li.active a {
    background-color: #177E89;
    border-radius: 39.5px;
  }
  .in-page-nav-bar .in-page-links li.active a:after {
    display: block;
  }
  .in-page-nav-bar .in-page-links li.active a:before {
    display: none;
  }
}

.in-page-nav-bar .in-page-links li.active a:before {
  content: "\f061";
  color: #13102c;
  font-family: 'Font Awesome 5 Pro';
  font-size: 15px;
  font-weight: 900;
}

.in-page-nav-bar .in-page-links li a {
  position: relative;
  display: block;
  font-weight: 500;
  padding: 0.75rem 1.25rem;
  text-align: left;
  transition: background 0.2s linear;
  color: #13102c;
  /*
				&:after {
					content: "\f0d8";
					position: absolute;
					bottom: -6px;
					left: 0;
					right: 0;
					margin: auto;
					font-family: 'Font Awesome 5 Pro';
					font-size: 30px;
					font-weight: 900;
					color: #fff;
					display: none;
				}
				*/
}

@media (min-width: 768px) {
  .in-page-nav-bar .in-page-links li a {
    color: #fff;
    border-radius: 39.5px;
    text-transform: capitalize;
    border: 0 solid transparent;
    background-color: transparent;
    margin-bottom: 0;
    /*padding: 25px 35px;*/
    padding: 0.9rem 2rem;
    text-align: center;
  }
  .in-page-nav-bar .in-page-links li a:hover {
    background-color: #177E89;
    border-radius: 39.5px;
  }
  .in-page-nav-bar .in-page-links li a:hover:after {
    display: block;
  }
}

.in-page-nav-bar .in-page-links li a span {
  transition: all 0.2s linear;
}

.hero {
  background-image: url(../../img/pattern/patt_4_blue.svg) !important;
  background-color: #084C61;
  background-repeat: repeat;
  background-size: 30px auto;
  color: white;
}

.hero .center-items {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hero h2 {
  color: white !important;
}

h1 {
  color: white;
}

h2:not(.guide-title):before {
  content: "//";
  position: relative;
  color: #177E89;
  left: -10px;
}

.btn-primary-navy {
  width: fit-content !important;
}

.average-cost .factors-row {
  justify-content: space-between !important;
}

.average-cost .factors-row .factor-card {
  max-width: 380px;
  border-radius: 15px;
  padding: 40px;
  box-shadow: 0px 2px 25px rgba(0, 0, 0, 0.09);
}

.average-cost .factors-row .factor-card .factor-title {
  color: #762057;
}

.average-cost .planners-row {
  justify-content: space-around;
}

.average-cost .planners-row .planner-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.average-cost .planners-row .planner-card .planner-content {
  text-align: left;
}

.payment-options {
  background-color: #fff;
  background-image: url(../../img/pattern/patt_9_yellow.svg);
  background-repeat: repeat;
  background-size: 30px auto;
  padding-bottom: 50px;
  padding-top: 30px;
  overflow-x: hidden;
}

.payment-options .img-backdrop {
  border-radius: 20px;
  box-shadow: -20px -20px #177E89, 0px 2px 25px rgba(0, 0, 0, 0.4);
}

.payment-options .left-text, .payment-options .right-text {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 30px;
}

.cost-guide {
  background-color: #084C61;
}

.cost-guide h2 {
  text-align: center;
  max-width: 800px;
  color: white !important;
  margin: auto;
}

.cost-guide .content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.cost-guide .content p {
  color: white !important;
}

.cost-guide .image, .cost-guide .content {
  padding: 40px;
}

.cost-guide .decoration {
  width: 130px;
  height: auto;
  position: absolute;
  transform: rotate(45deg);
}

@media (max-width: 768px) {
  .cost-guide .decoration {
    display: none;
  }
}

.next-steps .container {
  max-width: 580px;
  padding: 20px;
}

.next-steps .number {
  background-color: #084C61;
  border-radius: 50%;
  color: #fff;
  font-size: 2rem;
  height: 42px;
  margin: 0.8rem;
  text-align: center;
  width: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 768px) {
  .next-steps .line-start:after {
    content: '';
    width: 0;
    height: 444px;
    position: absolute;
    border: 2px solid #177E89;
    top: 30px;
    left: 46px;
    z-index: -1;
  }
}

.option-title::after {
  border-top: 3px solid #177E89;
  content: "";
  margin: 0 2rem;
  width: 62rem;
  top: 66px;
  position: absolute;
}

.btn-primary-purple {
  width: fit-content !important;
}

.btn-primary-white:hover {
  color: #084C61;
  text-decoration: underline;
  transition: all ease 0.2s;
}

.section-find-community {
  margin: 0 !important;
}

@media (max-width: 768px) {
  .py-5 {
    padding-bottom: 2rem !important;
    padding-top: 2rem !important;
  }
  .py-6 {
    padding-bottom: 2rem !important;
    padding-top: 2rem !important;
  }
  .factor-card {
    box-shadow: unset !important;
    padding: 2rem 1.6rem !important;
  }
  .option-restack {
    flex-direction: column-reverse;
  }
  .cost-guide .image {
    padding: 20px 40px !important;
  }
  .payment-options .pb-5 {
    padding-bottom: 0rem !important;
  }
}
